(function () {
    'use strict';

    angular
        .module('app')
        .controller('SidebarController', ['sidebarService', SidebarController]);

    function SidebarController (sidebarService) {
        var vm = this;
        vm.medias = [];

        _init();

        function _init() {
            sidebarService.getInstagramPictures().then(function(medias) {
                vm.medias = medias;
            });
        }
    }
})();
