(function () {
    'use strict';

    angular
        .module('app')
        .service('sidebarService', ['$http', sidebarService]);

    function sidebarService ($http) {

        function getInstagramPictures () {
            var accessToken = '146070981.4dd8feb.97c8fd8acf924b3fb641f160ba7a3589';
            var userId = 4771193178; // unzestedegourmandises
            var count = 8;
            var url = 'https://api.instagram.com/v1/users/' + userId + '/media/recent';

            url += '?access_token=' + accessToken + '&scope=public_content&count=' + count + '&callback=JSON_CALLBACK' ;
            return $http.jsonp(url).then(function(response) {
                return response.data.data;
            });

        }

        return {
            getInstagramPictures: getInstagramPictures
        };
    }
})();
