(function () {
    'use strict';

    angular
        .module('app')
        .directive('sidebar', sidebarDirective);

    function sidebarDirective () {
        return {
            bindToController: true,
            controller: 'SidebarController',
            controllerAs: 'ctrl',
            link: function link (scope) {},
            restrict: 'E',
            scope: {},
            templateUrl: 'sidebar/views/sidebar.html'
        };
    }
})();
